body {
  background-color: #f0f0f0;
  padding: 20px;
  font-family: Arial, Helvetica, sans-serif;
}

h1 {
  text-align: center;
}

.search-form {
  background-color: #5d5dd2;
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form input {
  width: 300px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
  padding: 10px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #28a745;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
}

.search-form button:hover {
  background-color: #218838;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  padding: 20px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: 60%;
}

.info {
  padding: 10px;
}

.info-item {
  margin: 5px 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  display: block;
}

.load-more:hover {
  background-color: #0056b3;
}

